export const APP_VERSION = "1.0.1"

// Funo para verificar se h nova verso disponvel
export const checkForUpdates = async (): Promise<boolean> => {
  try {
    // Tentar buscar a verso atual do servidor
    const response = await fetch('/version.json', { 
      cache: 'no-store',
      headers: {
        'Cache-Control': 'no-store, max-age=0, must-revalidate'
      }
    })
    
    if (response.ok) {
      const data = await response.json()
      return data.version !== APP_VERSION
    }
    
    // Para desenvolvimento/teste: simular nova verso disponvel
    // Remover esta linha em produo
    if (import.meta.env.DEV) {
      console.log(' Modo desenvolvimento: simulando nova verso disponvel')
      return true
    }
    
    return false
  } catch (error) {
    console.log('No foi possvel verificar atualizaes:', error)
    
    // Para desenvolvimento/teste: simular nova verso disponvel
    // Remover esta linha em produo
    if (import.meta.env.DEV) {
      console.log(' Modo desenvolvimento: simulando nova verso disponvel (erro na verificao)')
      return true
    }
    
    return false
  }
}

// Funo para forar reload da aplicao
export const forceReload = () => {
  console.log(' Forando reload da aplicao...')
  window.location.reload()
}
